/*GOOGLE FONTS*/

/*MAIN*/
body{
    margin: 0;
    padding: 0;
    background: #ffffff;
    
}
.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins', sans-serif;
    font-weight: 900px;
    font-size: 50px;
    color: #00ffbe;
}
#heading{
    padding-bottom: 10px;
    border-bottom: 3px solid rgb(66, 66, 66);
}
.signin-text{
    font-size: 21px;
}
.btn{
    background: #00ffbe;
    color: #000000;
    box-shadow: 0px 0px 20px #00ffbe;
    width: 100%;
}
.btn:hover{
    color: rgb(255, 255, 255);
    background: #000000;
    border: 2px solid #00ffbe;
    box-shadow: 0 0 0 #000000;
}
#img-grid{
    display: flex;
    justify-content: center;
    align-items: center;
}
.container{
    background: #0d0d0d;
}

@media (max-width: 765px) {
    
    #img-grid{
        display: none;
        margin: 0;
        padding: 0;
    }
}
/*GOOGLE FONTS*/

body{
    margin: 0;
    padding: 0;
    background: none;
}

.file-info{
    color: rgb(0, 0, 0);
    font-weight: bolder;
}
.false{
    display: flex;
    align-self: flex-start;
    margin-right: 225px;
}
.true{
    display: flex;
    align-self: flex-end;
    margin-right: 10px;
    flex-direction: row-reverse;
}
.posts{
    display: inline;
    display: flex;
    flex-direction: column-reverse;
    
}

.nav-tab{
    border: none;
    outline: none;
    background-color: rgb(0, 0, 0);
    -webkit-text-decoration-style: none;
            text-decoration-style: none;
}
.nav-link{
    font-size: 14px;
    border-radius: 6px;
}
.date{
    font-size: 10px;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.user-info{
    font-size: 11px;
    color: #fff;
}
.avatar-false{
    max-height: 551px;
    max-width: 55px;
    border: 3px solid rgb(0, 255, 0);
}
.avatar-true{
    max-height: 55px;
    max-width: 55px;
    border: 3px solid rgb(0, 255, 0);
}

  .tru{
    background-color: rgb(203, 209, 209);
  }
  .fals{
      background-color: rgb(211, 208, 208) ;
  }


  /* MESSAGE-TYPING-FIELD */
#message-typing-box{
    background: rgb(0, 0, 0);
    border-top: 2px solid rgb(66, 66, 66);
}
input.form-control{
    width: 100%;
    background-color: rgb(10, 10, 10);
    border: 2px solid rgb(0, 255, 0);
    font-family: 'Cascadia Code', sans-serif;
}
input.form-control:focus{
    border: none;
    outline: none;
    box-shadow: none;
}
.file-btn-label {
    background-color: rgb(0, 255, 0);
    color: white;
    padding: 6px;
    cursor: pointer;
    margin-top: 1rem;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
}
[type="file"] {
    height: 0;
    overflow: hidden;
    width: 0;
}
.button-send {
    background-color: rgb(0, 0, 0);
    border: none;
    outline: none;
    color: white;
    padding: 1px 12px;
    font-size: 20px;
    cursor: pointer;
}
.button-send:focus{
    border: none;
    outline: none;
}

@media (max-width: 700px) {
    
    .false{
        margin-right: 20px;
    }
    .nav-tab{
        border: none;
        color: rgb(0, 0, 0);
        -webkit-text-decoration-style: none;
                text-decoration-style: none;
    }
    .nav-link{
        font-size: small;
        max-width: 200px;
    }
    .date{
        font-size: 8px;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
    }
    .user-info{
        font-size: 10px;
        color: #fff;
    }
    [type="btn"]{
        max-width: 10%; 
    }
    .avatar{
        max-height: 40px;
        max-width: 40px;
        border: 3px solid rgb(0, 255, 0);
    }
    .nav-link{
        display: flex;
    flex-wrap: wrap;
    }
    
    
}
